.App{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home{
  display: grid;
  width: 100%;
  height: 80vh;
  justify-content: center;
  align-items: center;
  /* background-color: pink; */
}
.sidenav {
  height: 100%; /* 100% Full-height */
  width: 5%; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 1; /* Stay on top */
  top: 0; /* Stay at the top */
  left: 0;
  background-color: #e4d8d8; /* Black*/
  overflow-x: hidden; /* Disable horizontal scroll */
  padding-top: 60px; /* Place content 60px from the top */
  transition: 0.5s; /* 0.5 second transition effect to slide in the sidenav */
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.sidenav i{
  color:rgb(35, 22, 5);
}
.nav-link{
  margin-bottom: 50%;
  padding: 10%;
}
.main{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.firstMain{
  /* background-color: pink; */
  width: 350px;
  height: 90vh;

}
.videoMain{
  display: flex;
  flex-direction: row;
  /* background-color: brown; */
  position: relative;
}
.title{
  position: absolute;
  left:98%;
  top:20%;
  margin-left:5%;
  width: 300px;
  height: 100px;
  /* background-color: aqua; */
  color: rgb(35, 22, 5);
}


.secondMain{
  width: 100px;
  /* background-color: yellow; */
  margin-top: 0%;
  padding: 1%;
  /* margin-left:1%; */
 
}
.likeButton{
  width: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */

}
.dislikeButton{
  width: 30px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;

}
span{
  margin-left: 10px;
}
button{
  /* width: 40%; */
  border: none;
}

i{
  font-size: 25px;
}

